.search-input {
  display: flex;
  align-items: center;
  border: 1px solid lightblue;
  height: 30px;
  padding: 10px 30px;
  border-radius: 999px;
  width: 75vh;
  margin: 0 auto;
  margin-top: 40px;
  max-width: 560px;
}
.search-input > input {
  flex: 1;
  padding: 10px 20px;
  border: none;
  font-size: medium;
}
.search-input > input:focus {
  outline: none;
}
.search-input-icon {
  color: gray;
}
.search-buttons {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}
.search-buttons button {
  margin: 5px;
  padding: 17px 15px;
  background-color: #f8f8f8;
  border: 1px solid white;
  text-transform: inherit;
  color: #5f6368;
  border-radius: 50px;
  max-width: 60px;
}
.search-buttons button:hover {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  background-color: #f8f8f8;
  border: 1px solid #c6c6c6;
  color: #222;
  background-image: -webkit-linear-gradient(top, #f8f8f8, #f1f1f1);
}
.search-buttons-hidden {
  display: none !important;
}
.button-and-desk-container {
  display: flex;
  flex-direction: column;
  text-align: center;

  padding: 25px 20px 25px 30px;
  border-radius: 10px;
}
.button-and-desk-container p {
  color: rgb(114, 112, 112);
}
.button-and-desk-container:hover {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  background-color: #f8f8f8;
  border: 1px solid #c6c6c6;
  color: #222;
  background-image: -webkit-linear-gradient(top, #f8f8f8, #f1f1f1);
}
@media (max-width: 650px) {
  .home-input-container,
  .search-input {
    max-width: 90% !important;
  }
  .search-buttons {
    display: none;
  }
}
